import React from "react"
import { Link } from "gatsby"
import Header from "../components/header"
import "../styles/global.css"
import benji from "./benji_profile_pic.jpg"
import { View, Text } from "react-native";

export default function About() {
    return (
        <div className='page_div'>
            <Header title="About Me" />
            <View style={{flex: "0 1", flexDirection: 'row', justifyContent: "space-between"}}>
                <View style={{flexShrink: 1, marginRight: '10px'}}>
                    <p>Hi I'm Benji! I'm from London, and have been working here as a Python developer since finishing
                    a maths undergrad at Cambridge in 2020. I'll be moving to Zurich in September 2021 to start a
                    datascience MSc in ETH Zurich.
                    </p>
                    <p>As you can tell I like coding but am not very good at frontend! I made this website with react/gatsby
                    as a learning project and to help get hired as a software developer ;). My favourite coding language is Python
                    but I enjoy trying out new ones and building stuff.
                    </p>
                    <p>My hobbies include badminton, tennis, baking, sci-fi, games. I speak mandarin semi-fluently, and poor
                    german; looking to practice both!
                    </p>
                </View>
                <View>
                    <img src={benji} width="300px" alt="Benjamin Dayan" title="Benjamin Dayan"></img>
                </View>
            </View>
        </div>
    )
}